* {
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(22, 107, 38);
}

.child-div {
  width: 300px;
  margin: auto;
}

.app-logo {
  width: 300px;
  height: auto;
}

.addItems {
  margin-top: 20px;
}

.addItems input {
  width: 270px;
  padding: 10px;
}

.addItems i {
  font-size: 20px;
  cursor: pointer;
  position: absolute;
  margin-top: 10px;
  margin-left: -30px;
  color: rgb(22, 107, 38);
}

.showItems {
  margin-top: 20px;
}

.eachItems {
  width: 275px;
  padding: 10px;
  background-color: yellowgreen;
  border-top: 1px solid wheat;
}

.showItems i {
  font-size: 20px;
  cursor: pointer;
  position: absolute;
  margin-top: -30px;
  margin-left: 265px;
  color: rgb(22, 107, 38);
}

.removeAll {
  width: 295px;
  margin-top: 20px;
  padding: 10px;
  font-size: 18px;
  font-weight: bold;
  color: rgb(22, 107, 38);
  cursor: pointer;
}

.removeAll:hover {
  background-color: yellowgreen;
}